.popap {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  transition: all 1s ease-in;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.popap-body {
  min-height: 100%;
  justify-content: center;
  align-items: center;
  padding: 50px;
  display: flex;
}

.popap-content {
  color: #000;
  max-width: 900px;
  background-color: #fff;
  padding: 30px;
  transition: all .8s;
  position: relative;
  transform: perspective(600px)translate(0, -100%)rotateX(45deg);
}

.popap-close-btn {
  text-align: center;
  height: 24px;
  width: 24px;
  border: 1.5px solid #000;
  border-radius: 50%;
  font-size: 17px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.popap-close-btn:hover, .popap-close-btn:focus {
  color: #fff;
  background-color: #4d5ae5;
}

.popap-close-btn:active {
